<template>
    <div>
        <div class="kd-page-title">进行支付</div>
        <div class="kd-page-content-wrapper kd-page-padding wrap">
            <p>点击“公共支付”，扫描右侧二维码</p>
            <div>
                <img class="payTipImg" alt="" :src="leftImg" />
            </div>
            <div>
                <qriously class="payValue" :value="payQrCode" :size="250"/>
            </div>
        </div>
        <div class="kd-foot-button kd-foot-button-position" v-if="!$store.state.common.visibleTip">
            <kd-button icon="kedaIcon-policework" text="返回首页" type-class="home"/>
			<kd-button icon="kedaIcon-circleleft" text="上一步" :button-handle="goUp"/>
<!--            <kd-button text="下一步" :button-handle="goNext"/>-->
        </div>
    </div>
</template>

<script>
import payTip from '@/assets/qrcode/payTip.png';
import { mapMutations } from 'vuex';

export default {
    name: 'UnifiedPaymentAppear',
    data () {
        return {
            leftImg: payTip,
            intervalId: null
        };
    },
    props: {
        payQrCode: { type: String, default: '' },
        nextFunction: { type: Function, default: function () { this.changeStep('down'); } }
    },
    methods: {
        ...mapMutations('common', ['changeStep']),
        async goNext () {
            await this.nextFunction();
        },
        goUp () {
            this.changeStep('up');
        }
    },
    mounted () {
        this.intervalId = setInterval(async () => {
            await this.nextFunction();
        }, 2000);
    },
    beforeDestroy () {
        clearInterval(this.intervalId);
    }

};
</script>

<style lang="scss" scoped>
.wrap {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: flex-end;
    padding-bottom: 0;
    > p {
        width: 100%;
        text-align: center;
        font-size:32px;
    }
    > div {
        width: 50%;
        height: 79%;
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;
    }
    .payTipImg {
        height: 100%;
    }
    .payValue {
        background: #FFFFFF;
        max-width: 100%;
    }
}
</style>
