// 打印接口
import request from '@/utils/request';
import store from '@/store';
import { wsSend } from '@/utils/websocket';

export const tickPrint = async (params) => {
    wsSend(store.state.permanent.lightWebSocket, 'lightCtrl@B8:ON');
    const result = await request.ajax('http://localhost:9094/tickePtape', 'post', params, { isLocalHttp: true });
    wsSend(store.state.permanent.lightWebSocket, 'lightCtrl@B8:OFF');
    return new Promise(resolve => { resolve(result); });
};

export const tickPrintAll = async (params) => {
    wsSend(store.state.permanent.lightWebSocket, 'lightCtrl@B8:ON');
    const result = await request.ajax('http://localhost:9094/tickePtapeMulti', 'post', params, { isLocalHttp: true });
    wsSend(store.state.permanent.lightWebSocket, 'lightCtrl@B8:OFF');
    return new Promise(resolve => { resolve(result); });
};
