<template>
    <div>
        <div class="kd-page-title">进行支付</div>
        <div class="kd-page-content-wrapper kd-page-padding wrap">
            <p>打开微信或支付宝，扫一扫下方统一支付小程序</p>
            <div>
                <img alt="" :src="leftImg"/>
                <div>微信小程序</div>
            </div>
            <div>
                <img alt="" :src="rightImg"/>
                <div>支付宝小程序</div>
            </div>
        </div>
        <div class="kd-foot-button kd-foot-button-position" v-if="!$store.state.common.visibleTip">
            <kd-button icon="kedaIcon-policework" text="返回首页" type-class="home"/>
            <kd-button text="下一步" :button-handle="goNext"/>
        </div>
    </div>
</template>

<script>
import weixin from '@/assets/qrcode/weixin.jpg';
import zhifubao from '@/assets/qrcode/zhifubao.jpg';
import { mapMutations } from 'vuex';

export default {
    name: 'UnifiedPaymentChoose',
    data () {
        return {
            leftImg: weixin,
            rightImg: zhifubao
        };
    },
    props: {
        payQrCode: { type: String, default: '' },
        nextFunction: { type: Function, default: function () { this.changeStep('down'); } }
    },
    methods: {
        ...mapMutations('common', ['changeStep']),
        async goNext () {
            if (this.payQrCode) {
                this.changeStep('down');
            } else {
                await this.nextFunction();
            }
        }
    }

};
</script>

<style lang="scss" scoped>
.wrap {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    > p {
        width: 100%;
        text-align: center;
        font-size:32px;
    }
    > div {
        width: 50%;
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;
        > div {
            font-size: 26px;
            margin: 15px 0 0;
        }
        > img {
            height: 250px;
        }
    }
}
</style>
