<template>
    <div>
        <div class="contentText" @click="openSelect({isFirst:true})">{{lastData ? lastData : placeholder}}</div>
        <div v-if="appearSelect" class="selectWrap">
            <div class="top">
                <div class="left">
                    <span v-for="(item, index) in contentText" :key="index" @click="change(index)">{{item}}</span>
                    <span>请选择</span>
                </div>
                <div class="right">
                    <div class="outBut" @click="appearSelect=false">退出</div>
                    <div class="sureBut" @click="sureLastData">确定</div>
                </div>
            </div>
            <div class="content">
                <div class="firstItemWrap" v-loading="loading">
                    <div class="firstItem" v-for="(firstItem,firstIndex) in filterData" :key="firstIndex" @click="insertAddress(firstItem)">{{firstItem.name}}</div>
                </div>
                <div class="nextButWrap">
                    <div @click="upPage" v-if="canUp()">
                        <i class="kedaIconfont kedaIcon-xiangshang"></i>
                        <span>上一页</span>
                    </div>
                    <div @click="downPage" v-if="canDown()">
                        <i class="kedaIconfont kedaIcon-xiangxia"></i>
                        <span>下一页</span>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>

import { getCapitalSelect } from '@/service/common';

export default {
    name: 'CapitalSelect',
    props: {
        watchData: { type: Function },
        placeholder: { type: String, default: '请选择' }
    },
    data () {
        return {
            address: '',
            appearSelect: false,
            firstSelect: [],
            firstSize: 20,
            firstCurrentPage: 1,
            loading: false,
            contentText: ['', '', ''],
            contentId: [{}, {}, {}],
            currentStep: 0,
            lastData: ''
        };
    },
    computed: {
        filterData () {
            return this.firstSelect.slice(this.firstSize * (this.firstCurrentPage - 1), (this.firstSize * this.firstCurrentPage - 1));
        },
        totalPage () {
            return Math.ceil(this.firstSelect.length / this.firstSize) || 1;
        }
    },
    methods: {
        insertAddress (item) {
            if (this.currentStep === 0) {
                this.$set(this.contentText, 1, '');
                this.$set(this.contentText, 2, '');
            } else if (this.currentStep === 1) {
                this.$set(this.contentText, 2, '');
            }

            this.$set(this.contentText, this.currentStep, item.name);
            this.$set(this.contentId, this.currentStep, item);
            if (this.currentStep < 2) {
                this.currentStep++;
                this.openSelect(item);
            }
        },
        sureLastData () {
            this.appearSelect = false;
            this.lastData = this.contentText.join('');
            this.watchData(this.lastData);
        },
        getMailAddress (data) {
            this.address = data;
        },
        change (step) {
            this.currentStep = step;
            if (step === 0) {
                this.openSelect({});
            } else if (step === 1) {
                this.openSelect(this.contentId[0]);
            }
        },
        async openSelect (params) {
            if (params.isFirst) {
                this.currentStep = 0;
            }
            this.loading = true;
            this.appearSelect = true;
            const res = await getCapitalSelect({ pid: params.id });
            console.log(res);
            if (res && res.code === '0') {
                this.firstSelect = res.result.areaTypeDataList;
            }
            this.loading = false;
        },
        upPage () {
            this.firstCurrentPage--;
        },
        downPage () {
            this.firstCurrentPage++;
        },
        canUp () {
            if (this.firstCurrentPage > 1) {
                return true;
            }
        },
        canDown () {
            if (this.firstCurrentPage < this.totalPage) {
                return true;
            }
        }
    }
};
</script>

<style lang="scss" scoped>
    .contentText {
        height: 100%;
        display: flex;
        align-items: center;
        color: #636F93;
        font-size: 28px;
        background: rgba(255, 255, 255, 0.8);
        border-radius: 5PX;
        text-indent: 20px;
        border: 1PX solid #fff;
        box-sizing: border-box;
    }

    .selectWrap {
        position: fixed;
        z-index: 9999;
        left: 0;
        bottom: 0;
        height: 100vh;
        width: 100vw;
        background: rgba(0, 0, 0, 0.64);
        display: flex;
        flex-direction: column;
        justify-content: flex-end;
        box-sizing: border-box;

        .top {
            padding: 0 30PX;
            box-sizing: border-box;
            background: linear-gradient(180deg, rgba(197, 215, 234, 1) 0%, rgba(165, 191, 216, 1) 100%);
            display: flex;
            justify-content: space-between;

            .left {
                color: #3C4B78;
                display: flex;
                align-items: center;
                margin: 10PX 0;
                > span {
                    margin-right: 20px;
                }

                > span:nth-last-child(1) {
                    color: #3C4B78;
                    font-weight: bold;
                }
            }

            .right {
                display: flex;
                align-items: center;
                margin: 10PX 0;
                .outBut {
                    color: #ffffff;
                    padding: 0 20PX;
                    background: linear-gradient(180deg, rgba(137, 189, 242, 1) 0%, rgba(79, 153, 225, 1) 100%);
                    margin-right: 20px;
                    border-radius: 8PX;
                }

                .sureBut {
                    color: #ffffff;
                    padding: 0 20PX;
                    background: linear-gradient(180deg, rgba(251, 149, 39, 1) 0%, rgba(255, 115, 20, 1) 100%);
                    border-radius: 8PX;
                }
            }
        }

        .content {
            background: linear-gradient(360deg, rgba(45, 123, 203, 1) 0%, rgba(31, 74, 117, 1) 100%);
            padding: 20PX;
            box-sizing: border-box;

            .firstItemWrap {
                /*min-height: 400px;*/
                display: flex;
                flex-wrap: wrap;
                align-items: center;
                color: #ffffff;
            }

            .nextButWrap {
                display: flex;
                justify-content: center;
                align-items: center;
                color: #ffffff;

                > div {
                    margin: 0 20px;
                }

                i {
                    margin-right: 10px;
                    font-size: 24px;
                }

            }
        }

        .firstItem {
            width: calc((100% - 80PX) / 4);
            margin: 10PX;
            height: 56px;
            background: linear-gradient(180deg, rgba(109, 144, 174, 1) 0%, rgba(60, 104, 139, 1) 100%);
            border: 1PX solid #FFFFFF;
            box-shadow: 0 0 0 1PX #2B588F;
            color: #ffffff;
            display: flex;
            justify-content: center;
            align-items: center;
            box-sizing: border-box;
            border-radius: 5px;
        }
    }
</style>
