export const fingerTranslationYinchuan = (payload) => {
    const isLeft = Object.keys(LEFT_FINGER_POSITION).find((i) => {
        return i === String(payload.zwyZwdm);
    });

    const leftFingerInfo = {};
    const rightFingerInfo = {};

    leftFingerInfo.pos = isLeft ? payload.zwyZwdm : payload.zweZwdm;
    leftFingerInfo.pic = isLeft ? payload.zwyZwtxsj : payload.zweZwtxsj;
    leftFingerInfo.feature = isLeft ? payload.zwyZwtzsj : payload.zweZwtzsj;
    leftFingerInfo.quality = isLeft ? payload.zwyZwtxzlz : payload.zweZwtxzlz;

    rightFingerInfo.pos = !isLeft ? payload.zwyZwdm : payload.zweZwdm;
    rightFingerInfo.pic = !isLeft ? payload.zwyZwtxsj : payload.zweZwtxsj;
    rightFingerInfo.feature = !isLeft ? payload.zwyZwtzsj : payload.zweZwtzsj;
    rightFingerInfo.quality = !isLeft ? payload.zwyZwtxzlz : payload.zweZwtxzlz;

    return { leftFingerInfo, rightFingerInfo };
};

export const uploadFingerTranslationYinchuan = (payload) => {
    const uploadFingerInfo = {};

    const isLeft = Object.keys(LEFT_FINGER_POSITION).find((i) => {
        return i === String(payload.originFingerInfo.zwyZwdm);
    });

    uploadFingerInfo.zwyZwdm = isLeft ? payload.left.positionCode : payload.right.positionCode;
    uploadFingerInfo.zwyZwtxsj = isLeft ? payload.left.base64 : payload.right.base64;
    uploadFingerInfo.zwyJpeg = isLeft ? payload.left.picture : payload.right.picture;
    uploadFingerInfo.zwyZwtzsj = isLeft ? payload.left.feature : payload.right.feature;
    uploadFingerInfo.zwyZwtxzlz = isLeft ? payload.left.quantity : payload.right.quantity;

    uploadFingerInfo.zweZwdm = !isLeft ? payload.left.positionCode : payload.right.positionCode;
    uploadFingerInfo.zweZwtxsj = !isLeft ? payload.left.base64 : payload.right.base64;
    uploadFingerInfo.zweJpeg = !isLeft ? payload.left.picture : payload.right.picture;
    uploadFingerInfo.zweZwtzsj = !isLeft ? payload.left.feature : payload.right.feature;
    uploadFingerInfo.zweZwtxzlz = !isLeft ? payload.left.quantity : payload.right.quantity;

    return uploadFingerInfo;
};

export const fingerTranslation = (payload) => {
    const isLeft = Object.keys(LEFT_FINGER_POSITION).find((i) => {
        return i === String(payload.zwyzw);
    });

    const leftFingerInfo = {};
    const rightFingerInfo = {};

    leftFingerInfo.pos = isLeft ? payload.zwyzw : payload.zwezw;
    leftFingerInfo.pic = isLeft ? payload.zwyTxsj : payload.zweTxsj;
    leftFingerInfo.feature = isLeft ? payload.zwyTzsj : payload.zweTzsj;
    leftFingerInfo.quality = isLeft ? payload.zwyTxzlz : payload.zweTxzlz;

    rightFingerInfo.pos = !isLeft ? payload.zwyzw : payload.zwezw;
    rightFingerInfo.pic = !isLeft ? payload.zwyTxsj : payload.zweTxsj;
    rightFingerInfo.feature = !isLeft ? payload.zwyTzsj : payload.zweTzsj;
    rightFingerInfo.quality = !isLeft ? payload.zwyTxzlz : payload.zweTxzlz;

    return { leftFingerInfo, rightFingerInfo };
};

export const uploadFingerTranslation = (payload) => {
    const uploadFingerInfo = {};

    const isLeft = Object.keys(LEFT_FINGER_POSITION).find((i) => {
        return i === String(payload.originFingerInfo.zwyzw);
    });

    uploadFingerInfo.zwyZw = isLeft ? payload.left.positionCode : payload.right.positionCode;
    uploadFingerInfo.zwyimage = isLeft ? payload.left.base64 : payload.right.base64;
    uploadFingerInfo.zwyfeature = isLeft ? payload.left.feature : payload.right.feature;
    uploadFingerInfo.zwyZwtxzlz = isLeft ? payload.left.quantity : payload.right.quantity;

    uploadFingerInfo.zweZw = !isLeft ? payload.left.positionCode : payload.right.positionCode;
    uploadFingerInfo.zweimage = !isLeft ? payload.left.base64 : payload.right.base64;
    uploadFingerInfo.zwefeature = !isLeft ? payload.left.feature : payload.right.feature;
    uploadFingerInfo.zweZwtxzlz = !isLeft ? payload.left.quantity : payload.right.quantity;

    return uploadFingerInfo;
};

// 电子监控数据处理
export const illegalityElectronicSelectTranslation = (payload) => {
    const list = cloneDeep(payload.list);

    delete payload.list.monitorProcessingRecordDTOS;
    delete payload.list.monitorRecordIds;
    delete payload.list.status;

    return {
        holder: payload.list.holder,
        mobile: payload.list.mobile,
        licenceNo: payload.list.licenceNo,
        plateNumber: payload.list.plateNumber,
        recordArr: list.monitorProcessingRecordDTOS.map((item) => {
            const dataMatch = payload.selection.find((i) => { return i.jkbh === item.jkbh; });
            if (dataMatch) {
                return { ...dataMatch, ...item, ...payload.list };
            } else {
                return { ...item, ...payload.list };
            }
        }),
        totalFines: payload.totalFines,
        totalScore: payload.totalScore
    };
};

// 电子监控数据处理后的再次更新
export const illegalityElectronicSelectUpdate = (payload, selectMonitor) => {
    let recordArr = cloneDeep(selectMonitor.recordArr);
    // 更新决定书编号
    recordArr = recordArr.map((item) => {
        const i = payload.find((x) => {
            return x.monitorRecordId === item.monitorRecordId;
        });

        if (i) {
            const obj = { ...item, ...i, ...i.monitorProcessingRecordDTO };
            obj.illegalTimeFormat = moment(obj.illegalTime).format('YYYY年MM月DD日HH时mm分');
            obj.year = moment().format('YYYY');
            obj.month = moment().format('MM');
            obj.day = moment().format('DD');
            delete obj.monitorProcessingRecordDTO;
            return obj;
        }
    });
    selectMonitor.recordArr = recordArr;
    return selectMonitor;
};
