<template>
	<div>
		<div class="kd-page-title">{{topText}}</div>
		<div class="kd-page-content-wrapper wrap-info">
			<verify-code
					ref="verifyCode"
					@getVerifyCode="getVerifyCode"
					:phoneBusinessTip="phoneBusinessTip"
					:onlyNeedPhone="onlyNeedPhone"
			/>
		</div>
		<div class="kd-foot-button kd-foot-button-position" v-if="!$store.state.common.visibleTip">
			<kd-button icon="kedaIcon-policework" text="返回首页" type-class="home"/>
			<kd-button icon="kedaIcon-circleleft" text="上一步" :button-handle="goUp"/>
			<kd-button icon="kedaIcon-circleright" text="提交" :button-handle="nextStep"/>
		</div>
		<alert-msg v-if="alertOn" :alertSure="sureAlert" :alertCancel="cancelAlert" :tip-text="alertText"/>
	</div>
</template>

<script>
import { mapMutations } from 'vuex';
import alertMsg from '@/components/alert-msg';
import verifyCode from '@/components/verify-code';
// import audioCar from '@/assets/media/trafic/301.mp3';
// import {playAudio} from '@/utils/media';

export default {
    name: 'phone-upload',
    components: { alertMsg, verifyCode },
    props: {
        topText: { type: String, default: '联系方式' },
        phoneBusinessTip: { type: String, default: '请输入联系方式' },
        onlyNeedPhone: { type: Boolean, default: false },
        nextFunction: {
            type: Function,
            default: function () {
                this.changeStep('down');
            }
        }
    },
    data () {
        return {
            alertOn: false,
            phoneNumber: ''
        };
    },
    computed: {
        alertText () {
            return '请确认手机号 ' + this.phoneNumber + ' 是否正确！';
        }
    },
    methods: {
        ...mapMutations('common', ['changeStep', 'changeTipPage']),
        nextStep () {
            if (!this.$refs.verifyCode.submission()) {
                return false;
            }
            this.alertOn = true;
            this.phoneNumber = this.$refs.verifyCode.item.phoneNumber;
        },
        checkNextStatus () {
            return this.$refs.verifyCode.item.phoneNumber.length !== 11;
        },
        async sureAlert () {
            await this.nextFunction(this.phoneNumber);
        },
        getVerifyCode (phone) {
            this.phoneNumber = phone;
        },
        cancelAlert () {
            this.alertOn = false;
        },
        goUp () {
            this.changeStep('up');
        }
    }
};
</script>

<style lang="scss" scoped>
	.wrap-info {
		display: flex;
		justify-content: center;
		align-items: center;
	}
</style>
