<template>
	<div>
		<div class="kd-page-title">指纹采集</div>
		<div class="kd-page-content-wrapper wrap">
			<div class="left">
				<p>{{ '采集：' + FINGER_POSITION[historyFingerInfo.pos] }}</p>
				<img :src="FINGER_IMG[historyFingerInfo.pos]" alt=""/>
				<img class="machineFinger" :src="machineImg" alt=""/>
			</div>
			<div class="right">
				<p v-html="message"></p>
				<div class="fingerRightWrap">
					<div>
						<img :src="emptyFinger" :class="{'printWFlash': isCollecting}" alt="" @click="startCollection(true)"/>
						<p>{{collectTip}}</p>
					</div>
					<div v-if="realFinger">
						<img class="realFinger" :src="realFinger" alt=""/>
						<p>指纹数据</p>
					</div>
				</div>
			</div>
		</div>
		<alert-msg v-if="alertOn" :alertSure="sure" :tip-text="tipText" :disable-but-cancel="true" :disableButSure="true"/>
		<div class="kd-foot-button kd-foot-button-position" v-if="!$store.state.common.visibleTip">
			<kd-button icon="kedaIcon-policework" text="返回首页" type-class="home"/>
			<kd-button icon="kedaIcon-circleright" text="下一步" :button-handle="goNext" :but-disable="!newFingerObject.picture"/>
		</div>
	</div>
</template>

<script>

import finger from '@/assets/images/finger.png';
import fingerActive from '@/assets/images/finger_active.png';
import leftPrintMachine from '@/assets/images/finger/leftPrintMachine.jpg';
import rightPrintMachine from '@/assets/images/finger/rightPrintMachine.jpg';
import { mapState, mapMutations } from 'vuex';
import { wsSend, wsOnMessage } from '@/utils/websocket';
import { fingerFeatureContrast } from '@/service/common';
import { addPrefix } from '@/utils/tools';
import alertMsg from '@/components/alert-msg';
import audioFingerReady from '@/assets/media/common/22.mp3';
import audioFingerComplete from '@/assets/media/common/24.mp3';
import audioFingerFinish from '@/assets/media/common/38.mp3';
import audioFingerFailed from '@/assets/media/common/27.mp3';
import { playAudio } from '@/utils/media';
import { FINGER_POSITION, FINGER_IMG } from '@/constant/constant';
import successImg from '@/assets/images/wait_done.png';
import failImg from '@/assets/images/imgTexts.png';

export default {
    name: 'CollectFingerPrint',
    components: { alertMsg },
    props: {
        nextFunction: {
            type: Function,
            default: function () {
                this.changeStep('down');
            }
        },
        historyFingerInfo: {
            type: Object,
            default: () => {
            }
        },
        type: { type: Number }
    },
    data () {
        return {
            message: '提示：请用力按下指纹<br/>确保整个手指覆盖指纹仪！',
            FINGER_POSITION: FINGER_POSITION,
            FINGER_IMG: FINGER_IMG,

            emptyFinger: finger,
            machineImg: null,
            newFingerObject: {},
            realFinger: '',

            positionName: '',

            failNumber: 0,
            alertOn: false,
            tipText: '',
            isCollecting: false,
            collectTip: '',
            tipImg: null
        };
    },
    watch: {
        type () {
            this.failNumber = 0;
            this.alertOn = false;
            this.tipText = '';
            this.emptyFinger = finger;
            this.realFinger = '';
            this.collectTip = '';
            this.newFingerObject = {};
            this.isCollecting = false;
            this.tipImg = null;
            this.positionName = parseInt(this.historyFingerInfo.pos, 10) >= 16 ? 'left' : 'right';
            this.machineImg = this.positionName === 'left' ? leftPrintMachine : rightPrintMachine;
            this.startCollection();
        }
    },
    computed: {
        ...mapState('permanent', {
            fingerprintWebSocket: 'fingerprintWebSocket'
        })
    },
    mounted () {
        this.audioFingerReady = playAudio(audioFingerReady);
        this.audioFingerComplete = playAudio(audioFingerComplete);
        this.audioFingerFinish = playAudio(audioFingerFinish);
        this.audioFingerFailed = playAudio(audioFingerFailed);
        this.audioFingerReady.play();

        this.positionName = parseInt(this.historyFingerInfo.pos, 10) >= 16 ? 'left' : 'right';
        this.machineImg = this.positionName === 'left' ? leftPrintMachine : rightPrintMachine;
        this.startCollection();

        // this.collectionSuccess({ result: { picture: '2323' } });
    },
    beforeDestroy () {
        this.audioFingerReady.destroy();
        this.audioFingerComplete.destroy();
        this.audioFingerFinish.destroy();
        this.audioFingerFailed.destroy();
        wsOnMessage(this.fingerprintWebSocket, null);
        this.stopAll();
    },
    methods: {
        ...mapMutations('common', ['changeStep', 'changeTipPage']),
        sure () {
            this.alertOn = false;
            this.tipText = '';
        },
        startCollection (state) {
            if (state && this.isCollecting === true) {
                return false;
            } else {
                this.isCollecting = true;
                this.collectTip = '正在采集中';
                this.stopAll();
                wsOnMessage(this.fingerprintWebSocket, null);
                wsOnMessage(this.fingerprintWebSocket, this.getFingerPrint);
                wsSend(this.fingerprintWebSocket, { module: 'Finger', directive: 'Start', obj: { position: this.positionName, fingerCode: this.historyFingerInfo.pos } });
            }
        },
        async getFingerPrint (res) {
            if (res && res.code === 1) { // 录制成功
                if (res && res.result && res.result.position === this.positionName) {
                    if (parseInt(res.result.quantity, 10) >= 55) {
                        await this.contrastFeature(this.historyFingerInfo.feature, res.result.feature, res);
                    } else {
                        this.collectionFail('指纹录制质量过低，请继续采集');
                    }
                }
            } else {
                if (this.failNumber >= 3) { // 录制失败，判断是否超过三次，超过停止
                    this.collectionFail();
                } else { // 否则重新录制
                    this.failNumber = this.failNumber + 1;
                    this.startCollection();
                }
            }
        },
        async contrastFeature (first, two, resultFinger) {
            if (this.CONSTANT.disableFingerContrast) {
                this.collectionSuccess(resultFinger);
            }
            this.collectTip = '采集成功，正在比对中，请稍后';
            const res = await fingerFeatureContrast({
                finger1: first,
                finger2: two
            });
            if (res && res.code === 1 && res.result.isMatch) {
                this.collectionSuccess(resultFinger);
            } else {
                let msg = '指纹验证不通过，请重新录制';
                if (res.result) {
                    msg = '指纹相似度:' + res.result.similarity + ',' + msg;
                }
                this.collectionFail(msg);
            }
        },
        collectionSuccess (res) {
            this.stopAll();
            this.failNumber = 0;
            this.alertOn = true;
            this.tipImg = successImg;
            this.tipText = '采集验证通过！即将进入下一步';
            this.isCollecting = false;

            this.audioFingerFinish.play();
            this.newFingerObject = res.result;
            this.emptyFinger = fingerActive;
            this.realFinger = addPrefix(res.result.picture);
            setTimeout(() => {
                this.goNext();
            }, 1500);
        },
        collectionFail (message) {
            this.stopAll();
            this.failNumber = 0;
            this.alertOn = true;
            this.tipImg = failImg;
            this.tipText = message || '指纹录制失败，请重试!';
            this.isCollecting = false;

            this.audioFingerFailed.play();

            setTimeout(() => {
                this.alertOn = false;
            }, 2000);

            this.startCollection();
        },
        stopAll () {
            wsSend(this.fingerprintWebSocket, { module: 'Finger', directive: 'Stop', obj: { position: 'left' } });
            wsSend(this.fingerprintWebSocket, { module: 'Finger', directive: 'Stop', obj: { position: 'right' } });
        },
        goUp () {
            this.changeStep('up');
        },
        async goNext () {
            if (this.newFingerObject.picture) {
                await this.nextFunction({ ...this.newFingerObject, positionCode: this.historyFingerInfo.pos });
            }
        }
    }
};
</script>

<style scoped lang="scss">
	@keyframes flash {
		from {
			box-shadow: 0 0 30px #fff;
		}
		to {
			box-shadow: 0 0 10px #fff;
		}
	}

	.wrap {
		display: flex;

		.left {
			width: 40%;
			background: rgba(255, 255, 255, 0.1);
			border: 1px dashed #ffffff;
			display: flex;
			flex-direction: column;
			justify-content: center;
			align-items: center;
			padding: 0 20PX; /*no*/
			box-sizing: border-box;

			p {
				box-sizing: border-box;
				padding: 0 20PX 10PX; /*no*/
				font-size: 28px;
			}

			img {
				width: 150PX; /*no*/
			}

			.machineFinger {
				width: 100%;
				box-sizing: border-box;
				padding: 0 20PX; /*no*/
			}
		}

		.right {
			width: 60%;
			display: flex;
			flex-direction: column;

			> p {
				font-size: 30px;
				text-align: center;
				padding: 40PX 60PX 20PX; /*no*/
				line-height: 64PX;
			}

			.fingerRightWrap {
				flex: 1;
				display: flex;
				justify-content: center;
				align-items: center;

				> div {
					margin: 0 20PX; /*no*/
					text-align: center;

					p {
						font-size: 30px;
						text-align: center;
						padding-top: 10PX; /*no*/
					}
				}
			}

			.realFinger {
				width: 140PX; /*no*/
				height: 136PX; /*no*/
				background: #ffffff;
				border-radius: 8PX; /*no*/
			}
		}

		.printWFlash {
			animation: flash 0.5s;
			animation-direction: alternate;
			animation-iteration-count: infinite;
		}
	}
</style>
