<template>
    <div>
        <identify-card-page
            v-if="currentStep === steps.findIndex(step=>step.key === 'identifyCardPage')"
            :disableReadCard="businessTypeCode !== BUSINESS_EN_NAME_CODE.idCardOverdue"
            :disableInputName="true"
        />
        <process :type="1" v-if="currentStep === steps.findIndex(step=>step.key === 'checkCard')" :handle-business="checkCard"/>
        <process :type="2" v-if="currentStep === steps.findIndex(step=>step.key === 'getFingerInfo')" :handle-business="getFingerInfo"/>
        <photo-tip-second v-if="currentStep === steps.findIndex(step=>step.key === 'photoTip')" :nextFunction="photoNext"/>
        <photo-preview v-if="currentStep === steps.findIndex(step=>step.key === 'collectComplete')" :previewPrintPhoto="handlePic"/>

        <process :type="3" v-if="currentStep === steps.findIndex(step=>step.key === 'uploadChangePhoto')" :handle-business="uploadChangePhoto" :audio="processAudio1" waitText="照片正在提交中，请稍后......"/>
        <process :type="4" v-if="currentStep === steps.findIndex(step=>step.key === 'uploadTakePhoto')" :handle-business="uploadTakePhoto" :audio="processAudio1" waitText="照片正在提交中，请稍后......"/>

        <collect-finger-print
            :type="5"
            v-if="currentStep === steps.findIndex(step=>step.key === 'handCollectFirst')"
            :nextFunction="fingerUploadFirst"
            :historyFingerInfo="leftFingerInfo"
        />
        <collect-finger-print
            :type="6"
            v-if="currentStep === steps.findIndex(step=>step.key === 'handCollectSecond')"
            :nextFunction="fingerUploadSecond"
            :historyFingerInfo="rightFingerInfo"
        />
        <phone-upload v-if="currentStep === steps.findIndex(step=>step.key === 'phoneUpload')" :onlyNeedPhone="true" :nextFunction="phoneUpload"/>
        <mail-type v-if="currentStep === steps.findIndex(step=>step.key === 'mailType')" :nextFunction="mailAddressUpload"/>

        <process :type="7" v-if="currentStep === steps.findIndex(step=>step.key === 'uploadCash')" :handle-business="uploadCash" waitText="正在生成受理号，请稍后......"/>
        <process :type="8" v-if="currentStep === steps.findIndex(step=>step.key === 'commitPayOrder')" :handle-business="commitPayOrder" waitText="正在提交订单，请稍后......"/>
        <process :type="9" v-if="currentStep === steps.findIndex(step=>step.key === 'getQR')" :handle-business="getQR" waitText="正在生成支付二维码，请稍后......"/>

        <unified-payment-choose v-if="currentStep === steps.findIndex(step=>step.key === 'payChoose')" :payQrCode="payCode"/>
        <unified-payment-appear v-if="currentStep === steps.findIndex(step=>step.key === 'payResult')" :payQrCode="payCode" :money="money" :nextFunction="checkPayStatus"/>
        <process :type="10" v-if="currentStep === steps.findIndex(step=>step.key === 'processPrint')" :handle-business="identityCardGetPaperPath"/>
        <process :type="11" v-if="currentStep === steps.findIndex(step=>step.key === 'tickPrint')" :handle-business="tickPrint" :audio="processAudio2" waitText="业务凭条打印中，请稍后......"/>

        <process :type="12" v-if="currentStep === steps.findIndex(step=>step.key === 'tickPrintPhoto')" :handle-business="tickPrintPhoto" :audio="processAudio2" waitText="业务凭条打印中，请稍后......"/>
        <process :type="13" v-if="currentStep === steps.findIndex(step=>step.key === 'processComplete')" :handle-business="handleComplete"/>
        <complete v-if="currentStep === steps.findIndex(step=>step.key === 'complete')" :data="changeCardCompleteRes" footTexts="温馨提示：请取走业务申请表或凭条"/>

        <alert-msg v-if="alertOn" :alertSure="alertSure" :alertCancel="alertCancel" :tip-text="tipText" btnTextLeft="是" btnTextRight="否"/>
    </div>
</template>

<script>
import { mapState, mapMutations } from 'vuex';
import identifyCardPage from '@/view/public/before-work/identify-card-page';
import process from '@/components/process';
import photoTipSecond from '@/view/pageComponents/photo-tip-second';
import photoPreview from '@/view/pageComponents/photo-preview';
import phoneUpload from '@/view/pageComponents/phone-upload';
import unifiedPaymentChoose from '@/view/pageComponents/unified-payment-choose';
import unifiedPaymentAppear from '@/view/pageComponents/unified-payment-appear';
import alertMsg from '@/components/alert-msg';
import collectFingerPrint from '@/view/pageComponents/collect-finger-print';
import complete from '@/components/complete';
import mailType from '@/view/pageComponents/mail-type';
import { getIdentifyCheck, changeCardComplete, commitPayOrder, getOrderDetail, getFingerInfo, getAcceptance, uploadPicFtp, checkPayStatus, uploadMailInfo, submitFingerprint, identityCardGetPaperPath, faceContrastThirdPard } from '@/service/human/identify-card';
import audioObj from '@/assets/media/household/35.mp3';
import { handlePhotoRequest } from '@/utils/axiosHandle';
import { ID_NUMBER_CHANGE, ID_NUMBER_PHOTO_CHANGE } from '@/constant/stepsOption';
import { cancelPrefix } from '@/utils/tools';
import { logout } from '@/utils';
import { fingerTranslation, uploadFingerTranslation } from '@/utils/dataTranlation';
import { excelPrint } from '@/service/common';
import request from '@/utils/request';
import { tickPrint } from '@/service/HardwareRequest';
import { getIDPhotoTick } from '@/constant/tickertape';
import processAudio1 from '@/assets/media/household/39.mp3';
import processAudio2 from '@/assets/media/household/40.mp3';

export default {
    name: 'IdCardProcessingHfIndex', // 身份证-期满换证
    components: { identifyCardPage, alertMsg, photoTipSecond, process, collectFingerPrint, phoneUpload, photoPreview, unifiedPaymentChoose, unifiedPaymentAppear, complete, mailType },
    data () {
        return {
            changeCardCompleteRes: {},
            mp3: audioObj,
            handlePic: '',
            alertOn: false,
            tipText: '未查询到您的指纹信息，请在此机器进行身份证照片采集后,再前往人工窗口进行办理！<br/>立刻进行身份证照片采集',
            phone: '',
            paperPath: '',
            slh: '',
            payCode: '',
            money: '',
            uploadResult: {},
            processAudio1: processAudio1,
            processAudio2: processAudio2,
            originFingerInfo: {}, // 原始指纹
            leftFingerInfo: { pos: '16' },
            rightFingerInfo: { pos: '13' },
            newLeftFingerInfo: {},
            newRightFingerInfo: {},
            uploadFingerInfo: {}, // 更新后的指纹

            mailInfo: {}
        };
    },
    computed: {
        ...mapState('common', {
            steps: 'steps',
            currentStep: 'currentStep',
            identifyCard: 'identifyCard',
            businessTypeCode: 'businessTypeCode',
            businessTypeName: 'businessTypeName'
        }),
        ...mapState('permanent', {
            originBusinessObject: 'originBusinessObject',
            BUSINESS_EN_NAME_CODE: 'BUSINESS_EN_NAME_CODE',
            singleReflexWebSocket: 'singleReflexWebSocket'
        })
    },
    mounted () {
        this.setSteps(ID_NUMBER_CHANGE);
    },
    methods: {
        ...mapMutations('common', ['changeStep', 'setSteps', 'changeTipPage', 'setCurrentBusinessName', 'setUserRecognitionId', 'setUserBusinessId', 'setCurrentBusinessHandle']),
        async checkCard () {
            const res = await getIdentifyCheck({
                businessType: this.businessTypeCode,
                idNum: this.identifyCard.certNumber
            });
            if (res && res.code === '0') {
                if (res.status === 7005) {
                    this.tipText = '未查询到您的办理信息,请在此机器进行身份证照片采集后,再前往人工窗口进行办理！<br/>是否立刻进行身份证照片采集?';
                    this.alertOn = true;
                } else {
                    if (res.result.status === 1) { // paycode 获取成功,未支付
                        this.handleRepeatBusiness(res);
                        this.changeStep('payChoose');
                    } else if (res.result.status === 2) { // 支付成功，未办结
                        this.handleRepeatBusiness(res);
                        this.changeStep('processPrint');
                    } else {
                        this.changeStep('down');
                    }
                }
            } else if (res) {
                this.changeTipPage({ message: res.message });
            }
        },
        handleRepeatBusiness (res) {
            this.slh = res.result.slh;
            this.payCode = res.result.payCode;
            this.money = res.result.money;
            this.setUserRecognitionId(res.result.userRecognitionId);
            this.setUserBusinessId(res.result.businessId);
            this.setCurrentBusinessHandle(res.result.businessTypeCode);
            this.setCurrentBusinessName(this.originBusinessObject.find((i) => { return i.businessCode === res.result.businessTypeCode; }).businessName);
        },
        async getFingerInfo () {
            const res = await getFingerInfo();
            if (res && res.code === '0') {
                if (res.result && res.result.result === '1') {
                    // 无指纹特殊处理
                    this.tipText = '未查询到您的指纹信息或指纹信息异常,请在此机器进行身份证照片采集后,再前往人工窗口进行办理！<br/>是否立刻进行身份证照片采集?';
                    this.alertOn = true;
                } else {
                    this.originFingerInfo = JSON.parse(JSON.stringify((res.result)));

                    this.leftFingerInfo = fingerTranslation(res.result).leftFingerInfo;
                    this.rightFingerInfo = fingerTranslation(res.result).rightFingerInfo;

                    this.changeStep('down');
                }
            } else if (res) {
                this.changeTipPage({ message: res.message, enableBack: true });
            }
        },
        alertSure () {
            this.setCurrentBusinessName(this.originBusinessObject.find((i) => { return i.businessCode === 120; }).businessName); // 修改成照片采集业务
            this.setSteps(ID_NUMBER_PHOTO_CHANGE);
            this.alertOn = false;
            this.changeStep('photoTip');
        },
        alertCancel () {
            logout();
        },
        phoneUpload (phone) {
            this.phone = phone;
            this.changeStep('down');
        },
        async mailAddressUpload (mailInfo) {
            // { placeOfCollection, address, name, phone }
            this.mailInfo = mailInfo;
            const res = await uploadMailInfo(mailInfo);
            if (res && res.code === '0') {
                this.changeStep('down');
            } else {
                this.changeTipPage({ message: res.message, enableBack: true });
            }
        },
        uploadCash () {
            setTimeout(async () => {
                const res = await getAcceptance({ phone: this.phone });// 正在生成受理号，请稍后......
                if (res && res.code === '0') {
                    this.uploadResult = res.result;
                    this.payCode = res.result.payCode;
                    this.slh = res.result.slh;
                    this.money = res.result.money;
                    this.changeStep('down');
                } else {
                    this.changeTipPage({ message: res.message, enableBack: true });
                }
            }, 8000);
        },
        commitPayOrder () {
            setTimeout(async () => {
                const res = await commitPayOrder({ // 正在提交订单，请稍后......
                    slh: this.slh
                });// 提交订单
                if (res && res.code === '0') {
                    this.changeStep('down');
                } else {
                    this.changeTipPage({ message: res.message, enableBack: true });
                }
            }, 8000);
        },
        getQR () {
            setTimeout(async () => {
                const res = await getOrderDetail({ // 正在生成支付二维码，请稍后......
                    slh: this.slh
                });// 获取订单详情
                if (res && res.code === '0') {
                    this.changeStep('down');
                } else {
                    this.changeTipPage({ message: res.message, enableBack: true });
                }
            }, 8000);
        },
        async checkPayStatus () {
            const res = await checkPayStatus({
                slh: this.slh
            });// 查询是否支付成功
            if (res && res.code === '0') {
                this.changeStep('down');
            }
        },
        async identityCardGetPaperPath () {
            const res = await identityCardGetPaperPath({});
            if (res && res.code === '0') {
                this.paperPath = res.result.paperPath;
                this.changeStep('down');
            } else if (res) {
                this.changeTipPage({ message: res.message });
            }
        },
        async handleComplete () {
            const res = await changeCardComplete({});
            if (res && res.code === '0') {
                this.changeCardCompleteRes = res.result;
                this.changeStep('down');
            } else if (res) {
                this.changeTipPage({ message: res.message });
            }
        },
        async photoNext (chosePhoto) {
            const res = await handlePhotoRequest(chosePhoto, this.CONSTANT);
            if (res && res.code === 1) {
                this.handlePic = res.obj;
                this.changeStep('down');
            } else {
                this.changeTipPage({ message: res.message, enableBack: true });
            }
        },
        uploadChangePhoto () {
            setTimeout(async () => {
                const res = await faceContrastThirdPard({
                    photoStr: cancelPrefix(this.handlePic),
                    idNum: this.identifyCard.certNumber
                });
                if (res && res.code === '0') {
                    this.changeStep('down');
                } else {
                    this.changeTipPage({ message: res.message, enableBack: true });
                }
            }, 8000);
        },
        async uploadTakePhoto () {
            setTimeout(async () => {
                const res = await uploadPicFtp({
                    photoStr: cancelPrefix(this.handlePic),
                    idNum: this.identifyCard.certNumber
                });
                if (res && res.code === '0') {
                    this.changeStep('down');
                } else {
                    this.changeTipPage({ message: res.message, enableBack: true });
                }
            }, 8000);
        },
        async fingerUploadFirst (finger) {
            this.newLeftFingerInfo = finger;
            this.changeStep('down');
        },
        async fingerUploadSecond (finger) {
            this.newRightFingerInfo = finger;

            this.uploadFingerInfo = uploadFingerTranslation({
                originFingerInfo: this.originFingerInfo,
                left: this.newLeftFingerInfo,
                right: this.newRightFingerInfo
            });
            const res = await submitFingerprint(this.uploadFingerInfo);
            if (res && res.code === '0') {
                this.changeStep('down');
            } else {
                this.changeTipPage({ message: res.message, enableBack: true });
            }
        },

        async tickPrint () {
            const res = await excelPrint({
                path: request.getServerUrl() + this.paperPath
                // path: request.getServerUrl() + this.uploadResult.paperPath
            });
            if (res && res.code === 1) {
                this.changeStep('down');
            } else {
                this.changeTipPage({ message: '打印机出故障啦，请稍后重试' });
            }
            // const mail = COLLECTION_METHOD.find((item) => { return item.type === this.mailInfo.placeOfCollection; });
            // const data = {
            //     ...this.changeCardCompleteRes,
            //     slh: this.uploadResult.slh,
            //     lzfs: mail ? mail.value : '',
            //     slyy: ID_NUMBER_REPLACEMENT_TYPES[this.businessTypeCode],
            //     dwmc: this.uploadResult.sldw,
            //     cbr: this.uploadResult.cbr,
            //     certNumber: this.identifyCard.certNumber,
            //     partyName: this.identifyCard.partyName
            // };
            // const res = await tickPrint({ content: getIdNumberTick(data) });
            // this.changeStep('down');
            // console.log('打印凭条结果：', res);
        },
        async tickPrintPhoto () {
            const data = {
                certNumber: this.identifyCard.certNumber,
                partyName: this.identifyCard.partyName
            };
            const res = await tickPrint({ content: getIDPhotoTick(data) });
            this.changeStep('down');
            console.log('打印凭条结果：', res);
        }

    }
};
</script>
