<template>
    <div>
        <div class="kd-page-title">照片预览</div>
        <div class="kd-page-content-wrapper wrap">
            <img v-if="previewPrintPhoto" :src="getPhoto()" alt="">
            <img v-else :src="cameraDefault" alt="">
        </div>
        <div class="kd-foot-button kd-foot-button-position" v-if="!$store.state.common.visibleTip">
            <kd-button icon="kedaIcon-policework" text="返回首页" type-class="home"/>
            <kd-button v-if="!disableTake" icon="kedaIcon-circleleft" text="重拍" :button-handle="goUp"></kd-button>
            <kd-button icon="kedaIcon-circleleft" text="下一步" :button-handle="goNext"/>
        </div>
    </div>
</template>
<script>
import { mapMutations } from 'vuex';
import cameraDefault from '@/assets/images/default/cameraDefault.png';
import { addPrefix } from '@/utils/tools';

export default {
    name: 'PhotoPreview',
    data () {
        return {
            cameraDefault: cameraDefault,
            nextTitle: '下一步'
        };
    },
    props: {
        disableTake: { type: Boolean, default: false },
        previewPrintPhoto: String,
        nextFunction: { type: Function, default: function () { this.changeStep('down'); } }
    },
    methods: {
        ...mapMutations('common', ['changeStep']),
        async goNext () {
            await this.nextFunction();
        },
        goUp () {
            this.changeStep('photoTip');
        },
        getPhoto () {
            return addPrefix(this.previewPrintPhoto);
        }
    }
};
</script>

<style lang="scss" scoped>
    .wrap {
        display: flex;
        justify-content: center;
        align-items: center;
        img {
            max-width: 100%;
            max-height: 100%;
        }
    }
</style>
