<template>
	<div>
		<div class="kd-page-title">领取方式</div>
		<div class="kd-page-content-wrapper wrap-info">
			<el-form class="uploadLabel">
				<el-form-item label="" label-width="150px" class="firstLine">
					<el-radio-group v-model="placeOfCollection">
						<el-radio
								class="uploadChoose"
								v-for="(item,index) in placeOfCollectionArr"
								:key="index"
								:label="item.type"
								border
						>
							{{ item.value }}
						</el-radio>
					</el-radio-group>
				</el-form-item>
				<el-form-item label="" v-if="placeOfCollection === '10'" label-width="150px">
					<div class="textWrap" v-html="receivingAddress"></div>
				</el-form-item>
				<div class="oneLine" v-if="placeOfCollection === '30'">
					<el-form-item label="收件人：" label-width="150px">
						<kd-full-input class="mailInput" ref="nameInput" placeholder="收件人姓名" :watch-data="getMailName"/>
					</el-form-item>
					<el-form-item label="联系电话：" label-width="150px">
						<kd-full-input class="mailInput" ref="phoneInput" placeholder="收件人联系方式" :watch-data="getMailPhone" :imeType="3" :limitNumber="11"/>
					</el-form-item>
				</div>
				<el-form-item label="收件地址：" v-if="placeOfCollection === '30'" label-width="150px">
					<capital-select class="mailInput" style="margin-bottom: 20PX" ref="addressInput" placeholder="请选择所在地区" :watch-data="getMailAddressPrefix"/>
					<kd-full-input class="mailInput" ref="addressInput" placeholder="请输入详细地址" :watch-data="getMailAddressSuffix"/>
				</el-form-item>
			</el-form>
			<div class="mailTip" v-html="mailTip"></div>
		</div>
		<div class="kd-foot-button kd-foot-button-position" v-if="!$store.state.common.visibleTip">
			<kd-button icon="kedaIcon-policework" text="返回首页" type-class="home"/>
			<kd-button icon="kedaIcon-circleleft" text="上一步" :button-handle="goUp"/>
			<kd-button icon="kedaIcon-circleright" text="下一步" :button-handle="goNext" :but-disable="nextButStatus()"/>
		</div>
	</div>
</template>

<script>
import { mapMutations } from 'vuex';
import audioMail from '@/assets/media/household/29.mp3';
import { playAudio } from '@/utils/media';
// import { COLLECTION_METHOD } from '@/constant/constant';
import capitalSelect from '@/components/capital-select';
import { getSelfMailAddress } from '@/service/common';

export default {
    name: 'MailType',
    components: { capitalSelect },
    data () {
        return {
            placeOfCollection: '30',
            addressSuffix: '',
            addressPrefix: '',
            name: '',
            phone: '',
            placeOfCollectionArr: { type: '30', value: '邮寄' },
            receivingAddress: ''
        };
    },
    props: {
        mailTip: { type: String, default: '' },
        // receivingAddress: { type: String, default: '蚌埠市禹会区红旗四路交通派出所' },
        nextFunction: {
            type: Function,
            default: function () {
                this.changeStep('down');
            }
        }
        // placeOfCollectionArr: { type: Array, default: () => COLLECTION_METHOD }
    },
    async mounted () {
        this.audioMail = playAudio(audioMail);
        this.audioMail.play();

        const res = await getSelfMailAddress({});

        // if (res && res.code === '0' && res.result.selfPlace) {
        //     this.placeOfCollectionArr = [
        //         { type: '10', value: '自取' },
        //         { type: '30', value: '邮寄' }
        //     ];
        //     this.receivingAddress = res.result.selfPlace;
        // } else {
        //     this.placeOfCollectionArr = [
        //         { type: '30', value: '邮寄' }
        //     ];
        // }

        if (res && res.code === '0') {
            if (res.result.selfPlace && res.result.hasMachine) {
                this.placeOfCollectionArr = [
                    { type: '10', value: '自取' },
                    { type: '30', value: '邮寄' }
                ];
                this.receivingAddress = res.result.selfPlace;
                this.placeOfCollection = '10';
            } else if (res.result.selfPlace && !res.result.hasMachine) {
                this.placeOfCollectionArr = [
                    { type: '10', value: '自取' }
                ];
                this.receivingAddress = res.result.selfPlace;
                this.placeOfCollection = '10';
            } else if (!res.result.selfPlace && res.result.hasMachine) {
                this.placeOfCollectionArr = [
                    { type: '30', value: '邮寄' }
                ];
                this.placeOfCollection = '30';
            } else {
                this.changeStep('down');
            }
        }
    },
    beforeDestroy () {
        this.audioMail.destroy();
    },
    methods: {
        ...mapMutations('common', ['changeStep', 'changeTipPage']),
        nextButStatus () {
            if (this.placeOfCollection === '30') {
                return !(this.addressSuffix && this.addressPrefix && this.name && this.phone);
            } else {
                return false;
            }
        },
        goUp () {
            this.changeStep('up');
        },
        goNext () {
            this.nextFunction({
                placeOfCollection: this.placeOfCollection,
                address: this.addressPrefix + this.addressSuffix,
                name: this.name,
                phone: this.phone
            });
        },
        getMailName (data) {
            this.name = data;
        },
        getMailAddressPrefix (data) {
            this.addressPrefix = data;
        },
        getMailAddressSuffix (data) {
            this.addressSuffix = data;
        },
        getMailPhone (data) {
            this.phone = data;
        }
    }
};
</script>

<style lang="scss" scoped>
	.wrap-info {
		display: flex;
		flex-direction: column;
		justify-content: flex-start;

		.textWrap {
			width: 100%;
			background: rgba(255, 255, 255, 0.1);
			padding: 10px 40PX;
			border: 1px dashed #ffffff;
			box-sizing: border-box;

			span {
				font-weight: bold;
			}
		}

		.oneLine {
			display: flex;
			justify-content: space-between;

			.el-form-item {
				width: 50%;
			}
		}

		.mailInput {
			color: #636F93;
			height: 60px;
		}

		::v-deep .el-form-item__content {
			font-size: 24px;
			margin: 0;
		}

		.uploadLabel {
			width: 100%;
			padding-right: 140PX;
			box-sizing: border-box;

			.uploadChoose {
				width: 18vw;
			}
		}

		.firstLine {
			width: 100%;

			.el-radio-group {
				width: 100%;
				display: flex;
				justify-content: space-between;

				> label {
					width: 45%;
					height: 7vh;
					box-sizing: border-box;
					margin-right: 0;
					margin-left: 0;
					padding: 0 0 0 44px;
					display: flex;
					align-items: center;
				}
			}
		}

		.mailTip {
			font-size: 19px;
		}
	}
</style>
