<template>
	<div>
		<div v-if="step === 1">
			<div class="kd-page-title">拍照须知</div>
			<div class="kd-page-content-wrapper wrap">
				<div v-for="(item,index) in images" :key="index" :class="index===1 || index===4 ? 'centerBox' : ''">
					<div><img :src="item.image" alt=""></div>
					<p>{{item.text}}</p>
				</div>
			</div>
			<div class="kd-foot-button kd-foot-button-position" v-if="!$store.state.common.visibleTip">
				<kd-button icon="kedaIcon-policework" text="返回首页" type-class="home"/>
				<kd-button icon="kedaIcon-circleright" :text="'下一步' + (timeNext > 0 ? timeNext : '')" :button-handle="goDown" :but-disable="timeNext > 0"/>
			</div>
		</div>

		<div v-if="step === 2">
			<div class="kd-page-title">照片采集</div>
			<div class="kd-page-content-wrapper wrapSecond">
				<p>请调整座椅，使头像至于虚线框后，点击拍照后开始采集</p>
				<div class="photo-collection-box">
					<crop
							v-for="(i,index) in photoNumber"
							:id="index"
							:key="index"
							ref="photo"
							:photo-src="singleReflexImgSrc"
							:take-photo-src="singleReflexTakeImgSrc"
							:size="size"
							:click-img="clickImg"
							@startPhoto="startPhoto"
					/>
				</div>
			</div>
			<div class="kd-foot-button kd-foot-button-position" v-if="!$store.state.common.visibleTip">
				<kd-button icon="kedaIcon-policework" text="返回首页" type-class="home"></kd-button>
				<kd-button icon="kedaIcon-circleleft" text="上一步" :button-handle="goUp" :but-disable="isTaking || isSubmit"/>
				<kd-button icon="kedaIcon-zhaoxiangji" :text="takeText" :button-handle="startUp" :but-disable="isTaking || isSubmit"></kd-button>
				<kd-button icon="kedaIcon-circleright" text="提交" :button-handle="goNext" :but-disable="!chosePhoto"></kd-button>
			</div>
			<alert-msg v-if="alertOn" :alertSure="alertSure" :alertCancel="alertCancel" :tip-text="tipText"/>
		</div>
	</div>

</template>

<script>
import { mapMutations, mapState } from 'vuex';
import audioObj from '@/assets/media/common/8.mp3';
import { playAudio } from '@/utils/media';
import { wsSend } from '@/utils/websocket';
import tip1 from '@/assets/images/photo/tip1.png';
import tip2 from '@/assets/images/photo/tip2.png';
import tip3 from '@/assets/images/photo/tip3.png';
import tip4 from '@/assets/images/photo/tip4.png';
import tip5 from '@/assets/images/photo/tip5.png';
import tip6 from '@/assets/images/photo/tip6.png';
import crop from '@/components/crop-photo';
import alertMsg from '@/components/alert-msg';
import audioOne from '@/assets/media/common/18.mp3';
import audioTwo from '@/assets/media/common/19.mp3';
import audioThree from '@/assets/media/common/20.mp3';
import audioFinish from '@/assets/media/common/21.mp3';
import { cancelPrefix } from '@/utils/tools';

export default {
    name: 'PhotoTipSecond',
    components: { crop, alertMsg },
    props: {
        tipText: { type: String, default: '是否确认提交？' },
        size: { type: Array },
        openAlert: { type: Boolean, default: false },
        nextFunction: {
            type: Function,
            default: function () {
                this.changeStep('down');
            }
        },
        photoNumber: { type: Number, default: 3 }
    },
    computed: {
        ...mapState('permanent', {
            nowAvatar: 'singleReflexImgSrc',
            singleReflexWebSocket: 'singleReflexWebSocket',
            lightWebSocket: 'lightWebSocket',
            singleReflexImgSrc: 'singleReflexImgSrc',
            singleReflexTakeImgSrc: 'singleReflexTakeImgSrc'
        })
    },
    data () {
        return {
            step: 1,
            images: [
                { image: tip1, text: '必须：露出眼睛、眉毛；眼睛平视' },
                { image: tip6, text: '不能：化浓妆；戴发卡、耳饰、银饰等饰品' },
                { image: tip2, text: '不能：戴眼镜、美瞳；穿浅色上衣' },
                { image: tip3, text: '必须：露出耳朵、脖子；双肩水平' },
                { image: tip4, text: '不能：有碎发、盘发、披肩发；漏出牙齿' },
                { image: tip5, text: '不能：戴帽子、穿毛领、高领上衣' }
            ],
            chosePhoto: '',
            alertOn: false,
            finishTip: true,
            takeText: '拍照',
            isTaking: false, // 正在拍照变量
            isSubmit: false, // 正在提交变量
            timeNext: 5,
            timeId: null
        };
    },
    methods: {
        ...mapMutations('common', ['changeStep', 'changeTipPage']),
        ...mapMutations('permanent', ['setSingleReflexTakeImgSrc', 'setSingleReflexImgSrc']),
        goDown () {
            this.step = 2;
        },
        goUp () {
            this.step = 1;
        },
        startTimeout () {
            this.timeNext = 5;
            this.timeId = setInterval(() => {
                this.timeNext--;
                if (this.timeNext < 0) {
                    clearInterval(this.timeId);
                }
            }, 1000);
        },
        // 选择图片
        clickImg (id, resultSrc) {
            this.$refs.photo.forEach((thisVue, index) => {
                if (index === id) {
                    thisVue.changeActive(true);
                } else {
                    thisVue.changeActive(false);
                }
            });
            this.chosePhoto = resultSrc;
        },
        // 拍照触发事件
        startPhoto (index, status) {
            if (status) {
                this.isTaking = true;
                this.chosePhoto = '';
            }

            if (this.photoNumber === 1) {
                if (index === 0) {
                    this.audioOne.play();
                }
            } else {
                if (index === 0) {
                    this.audioOne.play();
                } else if (index === 1) {
                    this.audioTwo.play();
                } else if (index === 2) {
                    this.audioThree.play();
                }
            }
            this.setSingleReflexTakeImgSrc('');
            if (this.$refs.photo[index]) {
                this.$refs.photo[index].getImg();
            } else {
                this.takeText = '重拍';
                this.audioFinish.play();
                this.finishTip = true;
                if (this.photoNumber === 1) {
                    this.$refs.photo[0].getCropImg();
                }
                this.isTaking = false;
            }
        },
        startUp () {
            this.startPhoto(0, true);
        },
        async alertSure () {
            this.isSubmit = true;
            await this.nextFunction(cancelPrefix(this.chosePhoto));
            this.isSubmit = false;
            this.alertOn = false;
        },
        alertCancel () {
            this.alertOn = false;
        },
        async goNext () {
            if (this.openAlert) {
                this.alertOn = true;
            } else {
                this.isSubmit = true;
                await this.nextFunction(cancelPrefix(this.chosePhoto));
                this.isSubmit = false;
                this.alertOn = false;
            }
        }
    },
    mounted () {
        this.audioObj = playAudio(audioObj);
        this.audioOne = playAudio(audioOne);
        this.audioTwo = playAudio(audioTwo);
        this.audioThree = playAudio(audioThree);
        this.audioFinish = playAudio(audioFinish);

        this.audioObj.play();

        this.setSingleReflexImgSrc('');
        wsSend(this.singleReflexWebSocket, { module: 'Camera', directive: 'reOpen' });
        wsSend(this.singleReflexWebSocket, { module: 'Camera', directive: 'start' });
        wsSend(this.lightWebSocket, 'lightCtrl@B5:ON');
        this.startTimeout();
    },
    beforeDestroy () {
        this.audioObj.destroy();
        this.audioOne.destroy();
        this.audioTwo.destroy();
        this.audioThree.destroy();
        this.audioFinish.destroy();
        this.setSingleReflexImgSrc('');
        wsSend(this.lightWebSocket, 'lightCtrl@B5:OFF');
        wsSend(this.singleReflexWebSocket, { module: 'Camera', directive: 'stop' });
        clearInterval(this.timeId);
    }
};
</script>

<style scoped lang="scss">
	.wrap {
		display: flex;
		flex-wrap: wrap;
		padding-bottom: 0;

		> div {
			width: calc((100% - 20px) / 3);

			> div {
				background: rgba(255, 255, 255, 0.8);
				text-align: center;
			}

			img {
				width: 90%;
				vertical-align: bottom;
			}

			p {
				background: linear-gradient(180deg, rgba(112, 163, 216, 1) 0%, rgba(58, 132, 191, 1) 100%);
				padding: 5px 0;
				font-size: 14px;
				text-align: center;
			}
		}

		.centerBox {
			margin: 0 10px;
		}

	}

	.wrapSecond {
		padding-top: 0;
		padding-bottom: 0;
		display: flex;
		flex-direction: column;
		justify-content: space-around;

		> p {
			font-size: 32px;
			text-align: center;
		}
	}

	.photo-collection-box {
		display: flex;
		justify-content: space-around;
	}

	.finish-tip {
		font-size: 22px;
		margin-left: 110px;
	}
</style>
