import { idPicHandle } from '@/service/common';
import { cancelPrefix } from '@/utils/tools';
import axios from 'axios';

export const handlePhotoRequest = async (photo, CONSTANT) => {
    if (!CONSTANT.disableDogHandle) {
        let res = await axios.get('config/electronicDogData.json');
        const params = res.data;
        res = await idPicHandle({
            base64: cancelPrefix(photo),
            ...params
        });
        return res;
    } else {
        return { code: '0', obj: photo };
    }
};

